<template>
  <div>
    <mu-appbar style="width: 100%;" color="#4ed995" class="header">
      <!-- <a href="#/Navigation?id=0"> -->
      <mu-button icon slot="left" class="icon" size="20" @click="toBack()">
        <mu-icon value="keyboard_arrow_left"></mu-icon>
      </mu-button>
      <!-- </a> -->
      <span>数据详情</span>
      <mu-button icon slot="right" class="icon" size="20" @click="selectDataPic" id="selectStation">
        <img src="static/img/queryNews.png" alt srcset />
      </mu-button>
    </mu-appbar>
    <div class="Datadetailscontent">
      <div class="title">
        <span>数据图表</span>
      </div>
      <div id="mainHis"></div>
    </div>
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <mu-sub-header>
            <div class="queryBtn">筛选</div>
          </mu-sub-header>

          <mu-list-item>
            <div class="titleselect">
              <span>因子名称：</span>
            </div>
            <div class="select">
              <select name v-model="station" @change="close(station)">
                <option :value="item" v-for="(item,index) in stationContent" :key="index">{{item}}</option>
              </select>
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="btnBottom">
              <div class="left" @click="confirmQuery()">取消</div>
              <div class="right" @click="confirmQuery()">确定</div>
            </div>
          </mu-list-item>
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
  </div>
</template>
<script>
import historyEcharts from "../../../static/js/historyEcharts";
// import tableComponent from "../../components/tableComponent/index";
import AppBace from "../../../static/ajax";
export default {
  data() {
    return {
      token: "",
      list: [],
      arr: [],
      arrTime: [],
      name: "pH",
      num: 1,
      startTime: "",
      lastTime: "",
      stationContent: [
        "pH",
        "氨氮",
        "总氮",
        "总磷",
        "总铜",
        "总镍",
        "总氰化物",
        "总铬"
      ],
      open: false,
      station: "pH",
      waterstation:""
    };
  },
  methods: {
    selectDataPic() {
      // this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: selectStation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.stationContent, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择因子", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          // _this.station = selectArr[0];
          // _this.open = true;
          // var name = Echarts.switchData(selectArr[0]);
          // _this.initData(name);
          // console.log(JSON.stringify(selectArr))
          _this.forSelect(_this.list, selectArr[0]);
        }
      });
    },
    closeBottomSheet() {
      this.open = false;
    },
    openBotttomSheet() {
      this.open = true;
    },
    close(item) {
      console.log(item);
      this.open = false;
      this.forSelect(this.list, item);
    },
    confirmQuery() {
      this.open = false;
    },
    toBack() {
      this.$router.go(-1);
    },
    ToQuery() {
      this.open = true;
      //  this.forSelect(this.list, "氨氮");
    },
    async initDataTable() {
      var url = "/station/water-data/list";
      //  参数：starttime/waterstation/lasttime
      var waterstation = this.waterstation;
      if (true) {
        var starttime = this.startTime;
        var lasttime = this.lastTime;
      } else {
        var starttime = AppBace.filter(this.timeDate1);
        var lasttime = AppBace.filter(this.timeDate2);
      }
      var isExcel = "no";
      var pageNum = this.num;
      var pageSize = 50000000;

      var res = await AppBace.request(
        url,
        "POST",
        {
          starttime,
          lasttime,
          waterstation,
          isExcel,
          pageNum,
          pageSize
        },
        this.token
      );
       console.log("list23", res);
      this.list = res.data.data;
      await this.forSelect(this.list, this.name);
      //   this.spliceString(res.data.data);
      //   this.listNumber = res.data.totalpage;
      //   this.number = (this.list.length + 1) * 42 + 5;
     
    },
    forSelect(e, name) {
      var arr = [];
      var arrTime = [];
      var key = 0;
      var arrData = [];
      arrData = e;

      for (let index = 0; index < e.length; index++) {
        switch (name) {
          case "pH":
            var value = e[index].ph;
            var key = 9;
            break;
          case "氨氮":
            var value = e[index].ammoniaNitrogen;
            var key = 35;
            break;
          case "总氮":
            var value = e[index].nitrogen;
            var key = 45;
            break;
          case "总磷":
            var value = e[index].phosphorus;
            var key = 4;
            break;
          case "总铜":
            var value = e[index].cuprum;
            var key = 2;
            break;
          case "总镍":
            var value = e[index].nickel;
            var key = 1;
            break;
          case "总铬":
            var value = e[index].chromium;
            var key = 1.5;
            break;
          case "总氰化物":
            var value = e[index].prussiate;
            var key = 1;
            break;
        }
        arr.push(value);
        var time = e[index].gmtMonitor;
        time = time.slice(5, 16);
        arrTime.push(time);
      }
      this.arr = arr;
      this.arrTime = arrTime;
      historyEcharts.initEcharts(this.arr, this.arrTime, name, key);
    },
   
  },
  async mounted() {
    this.name = this.$route.query.id;
    this.token = sessionStorage.getItem("token");
    this.startTime = this.$route.query.startTime;
    this.lastTime = this.$route.query.lastTime;
    this.waterstation = this.$route.query.waterstation;
    await this.initDataTable(this.waterstation);
    // this.$refs.mychild.initDataTable(this.startTime, this.lastTime,this.waterstation);
  },
  components: {
    // "Y-tableComponent": tableComponent
  }
};
</script>
<style scoped>
#mainHis {
  width: 100%;
  height: 300px;
  background: white;
  margin-bottom: 10px;
}
.Datadetailscontent {
  padding: 10px;
  font-size: 14px;
}
.title {
  border-left: 3px #4ed995 solid;
  padding-left: 6px;
  margin-bottom: 10px;
}
.icon img {
  width: 28px;
  height: 28px;
}
.select {
  width: 100%;
  text-align: center;
}
.select select {
  width: 100%;
  border-radius: 5px;
  border: #ccc 1px solid;
  outline: none;
  height: 30px;
  text-align: center;
  display: flex;
  justify-content: center;
}
.titleselect {
  width: 30%;
}
.timeSelect {
  z-index: 9999;
  position: absolute;
  top: 0;
  height: 600px;
}
.queryBtn {
  /* border-left: #4ed995 3px solid; */
  border-bottom: #ccc 1px solid;
}
.btnBottom {
  display: flex;
  justify-content: space-between;
  width: 100%;
  background: #4ed995;
  height: 30px;
  align-items: center;
  color: white;
}
.btnBottom .left {
  background: #ccc;
  flex: 1;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btnBottom .right {
  background: #4ed995;
  display: flex;
  align-items: center;
  flex: 1;
  height: 30px;
  justify-content: center;
}
</style>


